.Repos-list {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}

.Repo {
    border: 1px solid #dedede;
    width: 20%;
    margin: 5px;
    text-align: center;
    border-radius: 10px;
    word-wrap: break-word;
}

.Repo a {
    text-decoration: none;
    color: #222;
}


